@media (min-width: 320px) {
  .header {
    &__top {
      padding-top: get-vw(11);
      padding-bottom: get-vw(11);
    }
    &__intro {
      font-size: $fz-mob-xxs;
      text-align: center;
      text-transform: uppercase;
    }
    &__bottom {
      background-color: $color-light;
      border-top: 1px solid $color-grey2;
      border-bottom: 1px solid $color-grey2;
      display: flex;
      justify-content: space-between;
      padding: get-vw(8) get-vw(16);

    }
    &__logo {
      max-width: get-vw(60);
    }
    &__contacts {
      display: flex;
      flex-direction: column;
      align-items: flex-end;

    }
    &__tel {
      font-size: $fz-mob-m;
      font-family: $ff-secondary;
      margin-bottom: 2px;
      font-weight: 700;
    }
    &__mail {
      font-size: $fz-mob-xs;
    }
  }
}