@media (min-width: 320px) {
  .footer {
    background-color: $color-light;
    padding: get-vw(14) get-vw(16);
    &__inner {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      font-size: $fz-mob-xs;
    }
    &__box {
      flex-basis: 50%;
      &:nth-of-type(3) {  //TODO вопрос с переносом
        text-align: right;
      }
      &:nth-of-type(-n+2) {
        text-align: left;
      }
      &:nth-of-type(2) {
        order: 3;
      }
    }
    &__policy {
      text-decoration: underline;
    }
  }
}