@media (min-width: 320px) {
  .quiz {
    &__page {
      display: flex;
      flex-direction: column;
      align-items: stretch;
    }
    &__title {
      font-size: $fz-mob-xxm;
      text-transform: uppercase;
      text-align: center;
      font-family: $ff-secondary;
      font-weight: 400;
      margin-bottom: get-vw(4);
    }
    &__options {
      flex-grow: 1;
      display: flex;
      margin-bottom: get-vw(20);
      &--images {
        flex-wrap: wrap;
        justify-content: space-between;
      }
      &--images > .quiz__item {
        flex-basis: calc(50% - #{get-vw(10)});
        flex-shrink: 0;
        flex-grow: 0;
      }
    }
    &__item {
      &.quiz-item--img:nth-of-type(n+3) {
        margin-top: get-vw(15);
      }
    }
    &__actions {

    }

  }

  .quiz-actions {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    &__prev {
      background-color: transparent;
      border: none;
      margin-right: get-vw(25);
      & > img {
        width: get-vw(27);
      }
    }
    &__next {
      background-color: transparent;
      border: 1px solid $color-accent;
      text-transform: uppercase;
      color: $color-accent;
      font-size: $fz-mob-xm;
      padding: get-vw(6) get-vw(30);
      transition: .25s;
      &:active {
        color: $color-light;
        background-color: $color-accent;
      }
    }
  }



  .quiz-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &--img {

    }
    &__img {
      margin-bottom: get-vw(4);
      width: 100%;
    }
    &__input {
      display: none;
    }
    &__title {
      font-size: $fz-mob-s;
      text-align: center;
    }
  }
}