@media (min-width: 320px) {
  .modal {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    // height: 100%;
    &__overlay {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
    }
    &__content {
      position: absolute;
      top: get-vw(32);
      right: get-vw(16);
      left: get-vw(16);
      bottom: get-vw(40);
      background-color: $color-light;
      display: flex;
      flex-direction: column;
      align-items: stretch;
      justify-content: space-between;
    }
    &__header {
      margin-bottom: get-vw(32);
      padding: get-vw(16) get-vw(34) get-vw(12);
      position: relative;
    }
    &__title {
      font-size: $fz-mob-xs;
      text-align: center;
    }
    &__progress {
      position: absolute;
      right: 0;
      left: 0;
      bottom: 0;
    }
    &__main {
      flex-grow: 1;
      padding: 0 get-vw(32) get-vw(20);
    }
    &__aside {
      display: flex;
      justify-content: space-between;
      background-color: $color-primary;
      border-top: 1px solid $color-grey2;
      & > div:first-child {
        border-right: 1px solid $color-grey2;
      }
    }
    &__director {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding: get-vw(6) get-vw(17) get-vw(8);
    }
    &__tooltip {
      flex-grow: 1;
      font-size: $fz-mob-xs;
      padding: get-vw(20) get-vw(13);
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &__logo {
      display: none;
    }
    &__director-img {}
    &__director-title {
      font-size: $fz-mob-s;
      text-transform: uppercase;
      font-family: $ff-secondary;
      margin-bottom: 1px;
      white-space: nowrap;
    }
    &__director-subtitle {
      font-size: $fz-mob-xs;
    }
    &__close {
      width: get-vw(12);
      height: get-vw(12);
      border: none;
      background-color: transparent;
      padding: 0;
      display: block;
      position: absolute;
      left: calc(50% - #{get-vw(6)});
      top: get-vw(12);
      transform: rotate(45deg);
      &::after, &::before {
        content: '';
        display: block;
        height: 1px;
        position: absolute;
        top: calc(50% - 0.5px);
        right: 0;
        left: 0;
        width: 100%;
        background-color: #cdcdcd;
        transform-origin: center;
      }
      &::before {
        transform: rotate(90deg);
      }
    }
  }
}