html {
  // height: 100%;
}
body {  //FIXME проблемы с высотой
  background-color: $color-primary;
  font-family: $ff-primary;
  // height: 100%;

}
.blured {
  filter: blur(5px);
}
.wrapper {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
  min-height: 100vh;
  height: 100%;
  position: relative;
  & .main {
    flex-grow: 1;
  }
}
.text-accent {
  color: $color-accent;
}
.btn {
  background-color: $color-accent;
  font-family: $ff-secondary;
  color: $color-light;
  border: none;
  display: block;
}
.progress-bar {
  &__line {
    background-color: $color-grey2;
    height: 1px;
    width: 100%;
    // position: relative;
  }
  &__ready {
    background-color: $color-accent;
    height: 100%;
    position: relative;
    &::after {
      content: attr(data-percent)'%';
      position: absolute;
      right: 0;
      top: 0.5em;
      transform: translateX(50%);
      font-size: $fz-mob-xxm;
      font-family: $ff-secondary;
      color: $color-accent;
    }
  }
}