@media (min-width: 320px) {
  .main {
    background: url(../img/bg/bg-mobile.png) no-repeat 60% bottom / cover;  //TODO подшаманить размер и положение
    padding-top: get-vw(32);
    position: relative;
    &__content {
      padding-top: get-vw(32);
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: space-between;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
    }
    &__text {
      text-align: right;
      padding-right: get-vw(16);
      padding-left: get-vw(16);
      flex-grow: 1;
    }
    &__intro {
      font-size: $fz-mob-s;
      margin-bottom: get-vw(9);
    }
    &__title {
      font-size: $fz-mob-l;
      font-family: $ff-secondary;
      text-transform: uppercase;
      font-weight: 400;
      line-height: 1.33;
    }
    &__features {
      order: 3;
      background-color: $color-primary;
      width: 100%;
      display: flex;  
      justify-content: space-between;
      align-items: flex-start;
      font-size: $fz-mob-s;
      text-align: center;
      padding: get-vw(14) get-vw(10) get-vw(20);
      counter-reset: featuresCount;
    }
    &__feature {
      display: flex;
      flex-direction: column;
      flex-basis: 32%;
      &::before {
        content: counter(featuresCount);
        counter-increment: featuresCount;
        order: -2;
        font-size: get-vw(32);
        line-height: 1;
        font-family: $ff-secondary;
        font-weight: 900;
        color: $color-grey;
        margin-bottom: get-vw(11);
      }
      &::after {
        content: '';
        height: 1px;
        width: 80%;
        margin-left: 10%;
        background-color: $color-grey2;
        order: -1;
        margin-bottom: 3px;
      }
    }
    &__btn {
      padding: get-vw(16);
      margin-right: get-vw(16);
      margin-left: get-vw(16);
      width: calc(100% - #{get-vw(32)});
      margin-bottom: get-vw(16);
    }
  }

  
}

